import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from "axios"; // Importamos Axios
import './MenuFunctions.css';
import Resumen from './Resumen.js';
import AgregarNegocio from './AgregarNegocio.js';
import EditarNegocio from './EditarNegocio.js';
import EliminarNegocio from './EliminarNegocio.js';
import AgregarPlato from './AgregarPlato.js';
import EditarPlato from './EditarPlato.js';
import EliminarPlato from './EliminarPlato.js';

const MenuFunctions = () => {
  const [activeComponent, setActiveComponent] = useState(""); // Controla qué componente se muestra
  const navigate = useNavigate();

  const loadComponent = (component) => {
    setActiveComponent(component);
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case "resumen":
        return <Resumen />; // Carga la interfaz de validación de usuarios
      case "agregarnegocio":
        return <AgregarNegocio />; // Carga la interfaz de monitoreo de negocios
      case "editarnegocio":
        return <EditarNegocio />; // Carga la interfaz de monitoreo de reseñas
      case "eliminarnegocio":
        return <EliminarNegocio/>;
      case "agregarplato":
        return <AgregarPlato/>;
      case "editarplato":
        return <EditarPlato/>;
      case "eliminarplato":
        return <EliminarPlato/>;
      default:
        return (
          <div className="dashboard">
            <h2>¡Bienvenido al Panel de Funciones!</h2>
            <p>Selecciona una opción del menú para empezar.</p>
          </div>
        );
    }
  };

  const handleLogout = async () => {
    try {
      // Realizamos la solicitud POST con Axios
      const response = await axios.post("https://api-degusto.onrender.com/api/user/logout");

      if (response.status === 200) {
        // Redirige al componente LoginRegister.js
        console.log('Sesión cerrandose');
        navigate("/loginregister"); // Cambia la ruta según cómo esté definida en tu sistema
      } else {
        console.error("Error al cerrar sesión:", response.data);
        alert("Hubo un problema al cerrar sesión.");
      }
    } catch (error) {
      console.error("Error de red al cerrar sesión:", error);
      alert("Error de red. Por favor, intenta nuevamente.");
    }
  };

  return (
    <div className="owner-container">
      {/* Barra lateral */}
      <nav className="owner-menu">
        <h3 className="owner-title">Panel de Funciones</h3>
        <ul className="owner-list">
          <li
            className={activeComponent === "resumen" ? "active" : ""}
            onClick={() => loadComponent("resumen")}
          >
            Resumen
          </li>
          <li
            className={activeComponent === "agregarnegocio" ? "active" : ""}
            onClick={() => loadComponent("agregarnegocio")}
          >
            Agregar Negocio
          </li>
          <li
            className={activeComponent === "editarnegocio" ? "active" : ""}
            onClick={() => loadComponent("editarnegocio")}
          >
            Editar Negocio
          </li>
          <li
            className={activeComponent === "eliminarnegocio" ? "active" : ""}
            onClick={() => loadComponent("eliminarnegocio")}
          >
            Eliminar Negocio
          </li>
          <li
            className={activeComponent === "agregarplato" ? "active" : ""}
            onClick={() => loadComponent("agregarplato")}
          >
            Agregar Plato
          </li>
          <li
            className={activeComponent === "editarplato" ? "active" : ""}
            onClick={() => loadComponent("editarplato")}
          >
            Editar Plato
          </li>
          <li
            className={activeComponent === "eliminarplato" ? "active" : ""}
            onClick={() => loadComponent("eliminarplato")}
          >
            Eliminar Plato
          </li>
        </ul>
      </nav>

      {/* Contenido */}
      <div className="owner-main">
        <header className="owner-header">
          <span>Bienvenido, Administrador</span>
          <button className="btn-logout" onClick={handleLogout}>Cerrar Sesión</button>
        </header>
        <div className="content">{renderComponent()}</div>
      </div>
    </div>
  );
};

export default MenuFunctions;